.App {
	text-align: center;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

.frame {
	background-color: #dee2e6;
	border: solid 2vmin #eee;
	border-bottom-color: #fff;
	border-left-color: #eee;
	border-radius: 2px;
	border-right-color: #eee;
	border-top-color: #ddd;
	box-shadow: 0 0 5px 0 rgba(0, 0, 0, .25) inset, 0 5px 10px 5px rgba(0, 0, 0, .25);
	box-sizing: border-box;
	display: inline-block;
	/* margin: 10vh 10vw;
	padding: 8vmin; */
	position: relative;
	text-align: center;
}

.frame:before {
	border-radius: 2px;
	bottom: -0.3vmin;
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .25) inset;
	content: "";
	left: -0.3vmin;
	position: absolute;
	right: -0.3vmin;
	top: -0.3vmin;
}

.frame:after {
	border-radius: 2px;
	bottom: -0.7vmin;
	box-shadow: 0 2px 5px 0 rgba(0, 0, 0, .25);
	content: "";
	left: -0.7vmin;
	position: absolute;
	right: -0.7vmin;
	top: -0.7vmin;
}

.vertical-align-middle {
	vertical-align: middle !important;
}

.font-130-per {
	font-size: 130%;
}

.font-80-per {
	font-size: 80% !important;
}

.m-r-l-none {
	margin-right: 0px !important;
	margin-left: 0px !important;
}

.p-r-l-none {
	padding-right: 0px !important;
	padding-left: 0px !important;
}

.flex-center {
	display: flex;
	justify-content: center;
	align-items: center;
}

.box-shadow-28 {
	box-shadow: 0 1px 9px 1px rgba(0, 0, 0, 0.28);
	-webkit-box-shadow: 0 1px 9px 1px rgba(0, 0, 0, 0.28);
	-moz-box-shadow: 0 1px 9px 1px rgba(0, 0, 0, 0.28);
}

.p-t-b-1-5rem {
	padding-top: 1.5rem !important;
	padding-bottom: 1.5rem !important;
}

.border-collapse-separate {
	border-collapse: separate;
	border-spacing: 0px 1.75rem;
}

.m-b-1rem {
	margin-bottom: 0.6rem;
}

.max-width-800 {
	max-width: 800px;
}

.max-width-450 {
	max-width: 450px;
}

.cursor-pointer {
	cursor: pointer;
}

.border-radius-10 {
	border-radius: 10px;
}

.background-login {
	background-image: url("/reactjs_redux_example/images/background-login.jpg");
}

.background-white {
	background: #fff;
}

.padding-30 {
	padding: 30px;
}

@media only screen and (max-width: 767px) {
	.p-r-l-10-vw {
		padding-right: 10vw !important;
		padding-left: 10vw !important;
	}
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}

/* ---------------------------------------------------
    SIDEBAR STYLE
----------------------------------------------------- */
.overlay {
	display: none;
	position: fixed;
	width: 100vw;
	height: 100vh;
	background: rgba(0, 0, 0, 0.7);
	z-index: 998;
	opacity: 0;
	transition: all 0.5s ease-in-out;
}
.overlay.active {
	display: block;
	opacity: 1;
}

#sidebar {
	width: 250px;
	position: fixed;
	top: 0;
	left: -250px;
	height: 100vh;
	z-index: 999;
	background: #7386d5;
	color: #fff;
	transition: all 0.3s;
	overflow-y: auto;
	box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.2);
}

#sidebar.active {
	left: 0;
}

#dismiss {
	width: 35px;
	height: 35px;
	line-height: 35px;
	text-align: center;
	background: #7386d5;
	position: absolute;
	top: 10px;
	right: 10px;
	cursor: pointer;
	-webkit-transition: all 0.3s;
	-o-transition: all 0.3s;
	transition: all 0.3s;
}

#dismiss:hover {
	background: #fff;
	color: #7386d5;
}

#sidebar .sidebar-header {
	padding: 20px;
	background: #6d7fcc;
}

#sidebar ul.components {
	padding: 20px 0;
	border-bottom: 1px solid #47748b;
}

#sidebar ul p {
	color: #fff;
	padding: 10px;
}

#sidebar ul li a {
	padding: 10px;
	font-size: 1.1em;
	display: block;
}

#sidebar ul li a:not(.active):hover {
	color: #7386d5 !important;
	background: #fff;
}

#sidebar ul li a.active:hover {
	color: #fff !important;
}

#sidebar ul li.active > a,
a[aria-expanded="true"] {
	color: #fff;
	background: #6d7fcc;
}

a[data-toggle="collapse"] {
	position: relative;
}

.dropdown-toggle::after {
	display: block;
	position: absolute;
	top: 50%;
	right: 20px;
	transform: translateY(-50%);
}

ul ul a {
	font-size: 0.9em !important;
	padding-left: 30px !important;
	background: #6d7fcc;
}

ul.CTAs {
	padding: 20px;
}

ul.CTAs a {
	text-align: center;
	font-size: 0.9em !important;
	display: block;
	border-radius: 5px;
	margin-bottom: 5px;
}

a.download {
	background: #fff;
	color: #7386d5;
}

a.article,
a.article:hover {
	background: #6d7fcc !important;
	color: #fff !important;
}

nav#sidebar ul.components a {
	color: #fff;
	text-decoration: none;
	font-weight: 500;
}

/* ---------------------------------------------------
     SIDEBAR SCROLL STYLE
----------------------------------------------------- */

#sidebar::-webkit-scrollbar {
	width: 6px;
}

#sidebar::-webkit-scrollbar-track {
	border-radius: 10px;
}

#sidebar::-webkit-scrollbar-thumb {
	border-radius: 10px;
	background: rgba(255, 255, 255, 0.35);
}

